.components_mainbox {
  @include md {
    @include flex-center;
    display: flex;
    // flex-direction: column;
    // visibility: hidden;
  }
  width: 98.5%;
  display: grid;
  margin: 0 0;
  grid-template-columns: 0.8fr 1vw 1fr 1fr;
  grid-template-rows: 15% 22% 28% 10% 20%;
  gap: 10px 10px;
  grid-template-areas:
    "welcome_message welcome_message welcome_message welcome_message"
    "widget_sickleave widget_sickleave widget_holidays widget_homeoffice"
    "widget_team widget_tasks widget_tasks widget_calendar"
    "widget_team widget_tasks widget_tasks widget_announcements"
    "widget_applications widget_applications widget_birthdays widget_announcements";
  grid-area: components_mainbox;
}
